.videoContainer {
  width: 100%;
  display: inline-block;
}
.videoWrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* padding-top: 25px; */
  height: 0;
}

.videoWrapper iframe {
  max-height: 295px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


@media (max-width: 768px) {
  .App-moviePage--container {
    display: flex;
    flex-direction: column;
  }
  .videoContainer {
    width: 100vw;
    order: 0;
  }
  .App-moviePage--poster {
    height: 300px;
    order: 1;
   margin: 0 auto;
  }
  .App-moviePage--buttons {
    order: 2;
  }
}