img {
  max-width: 100%;
  display: block;
}

.App-slide {
  display: grid;
  width: 300px;
  box-shadow: var(--shadow-2);
}
/*  */
/*------------------- app slide -------------------*/
.App-slide--container {
  display: grid;
  grid-template-columns: auto 1fr;
}
@media (max-width: 768px) {
  .App-slide--container {
    grid-template-columns: 1fr;
  }
  .App-slide {
    width: 100%;
    justify-content: center;
    box-shadow: none;
  }
 
}

.App-slide--aside {
  padding: 1rem;
  background-color: #171717;
  display: flex;
  flex-direction: column;
}
/*  */
.App-slide--items {
  position: relative;
  grid-area: 1/1;
}
.App-slide--aside-title,
.App-slide--aside-paragraph,
.App-slide--aside-links {
  position: relative;
}
.App-slide--nav {
  grid-area: 1/1;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto 1fr;
}
.App-slide--nav button {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  opacity: 0;
  color: var(--secondary);
}
.App-slide--nav button:hover {
  background-color: rgba(255, 255, 255, 0.05);
  opacity: 0.4;
}
.App-slide--button-next {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.App-slide--items > *,
.App-slide--aside-title > *,
.App-slide--aside-paragraph > *,
.App-slide--aside-links > * {
  position: absolute;
  top: 0px;
  opacity: 0;
  pointer-events: none;
}

.App-slide--poster {
  background-repeat: no-repeat !important;
  background-size: cover !important;
  height: 400px !important;
  width: 300px !important;
  background-position: center center !important;
}

.App-slide--items > .active,
.App-slide--aside-title > .active,
.App-slide--aside-paragraph > .active,
.App-slide--aside-links > .active {
  pointer-events: initial;
  position: relative;
  opacity: 1;
}
.App-slide--thumb {
  display: flex;
  grid-column: 1/3;
}
.App-slide--thumb > span {
  flex: 1;
  display: block;
  height: 3px;
  background-color: rgba(0, 0, 0, 0.4);
  margin: 5px;
  border-radius: 3px;
  overflow: hidden;
}
.App-slide--thumb > span.active::after {
  content: '';
  display: block;
  height: inherit;
  background-color: rgba(255, 255, 255, 0.8);
  transform: translateX(-100%);
  border-radius: 5px;
  animation: thumb 5s forwards linear;
}

@keyframes thumb {
  to {
    transform: initial;
  }
}
