:root {
  --primary: #6930c3;
  --primary-var: #834d9b;
  --secondary: #f1e6fe;
  --tertiary: #161616;
  --success: #198754;
  --warning: #ffc107;
  --error: #da1e28;
  --info: #0f62fe;
  --gray: #c6c6c6;
  --light-gray: #8d8d8d;
  --white: #fff;

  --pink: #f53d6b;

  --border: 4px;
  --border-color: #6c757d;
  --border-color-dark: #191919;

  --font: 'Open Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

  --shadow-1: 0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
  --shadow-2: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  --shadow-3: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  --shadow-4: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

  --color-neutral-20: #cdcdcd;
  --color-neutral-60: #686868;
  --color-neutral-70: #2c2c2c;
  --color-neutral-80: #282828;

  --xxxl: 4.5rem;
  --xxl: 1.75rem;
  --xl: 1.5rem;
  --lg: 1.25rem;
  --md: 1.125rem;
  --sm: 1rem;
  --btn: 0.9rem;
  --xsm: 0.8rem;

  --opacity-15: rgba(0, 0, 0, 0.15);
  --opacity-50: rgba(0, 0, 0, 0.5);
  --opacity-90: rgba(0, 0, 0, 0.9);
  --opacity-98: rgba(0, 0, 0, 0.98);

  --border-radius: 4px;
}

/*------------------- app menu -------------------*/
.App-menu--modal {
  position: fixed;
  top: 0;
  left: 0;
  background: var(--opacity-98);
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.App-menu--content {
  width: 55vw;
}
@media (max-width: 768px) {
  .App-menu--content {
      width: 100vw;
  }
}
.App-menu--content li a {
  font-size: 1.25rem;
  display: block;
  padding: 1rem;
  margin: 0.5rem 0;
  border-bottom: 1px solid var(--color-neutral-60);
  color: var(--color-neutral-60);
}
.App-menu--content li a:hover {
  color: var(--primary);
  border-bottom: 1px solid var(--primary);
}
.App-menu-button--close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1001;
  /* margin: 0.5rem; */
}
@media (max-width: 768px) {
  .App-menu-button--close {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1001;
  }
  .App-menu--content-header {
    margin-top: 2.1rem;
  }
}
/*------------------- app card 2 -------------------*/
.App-card-2 {
  color: var(--secondary);
  border: 1px solid var(--opacity-15);
  border-radius: var(--border-radius);
  transition: 0.2s ease-in-out;
  width: 179px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 768px) {
  .App-card-2 {
      width: 150px !important;
  }
}
.App-card-2:active {
  border: 1px solid var(--primary);
}
.App-card-2:hover {
  box-shadow: var(--shadow-2);
}
/* .App-card--image-container {
  height: 248px;
  overflow: hidden;
} */
.App-card-2 img {
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
}
.App-card-2 section {
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.App-card-2 h4 {
  font-size: var(--sm);
  letter-spacing: 0.03rem;

  margin-top: 0.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
}
.App-card-2 p {
  line-height: 1.5;
  font-size: 0.9rem;
  font-weight: 400;
  color: var(--color-neutral-20);
  flex: 1;
}

/*------------------- search -------------------*/
.App-search--container {
  position: relative;
}
.App-search--button {
  position: absolute;
  top: 0.2rem;
  right: 0.2rem;
}
.App-poster-mini {
  /* width: 50px; */
  height: 65px;
}
.App-search-result {
  position: absolute;
  background: var(--color-neutral-20);
  z-index: 500;
  width: 100%;
  box-shadow: var(--shadow-1);
}
.App-search-result--body p {
  font-size: var(--xsm);
  margin: 0;
}
/*------------------- app details -------------------*/
.App-details p {
  padding: 0.5rem 0;
  margin: 0;
  border-bottom: 1px solid var(--opacity-15);
}
.App-details p:first-child {
  border-top: 1px solid var(--opacity-15);
}

.App-details b {
  font-weight: 700;
  margin-right: 0.75rem;
}
/*------------------- app maze -------------------*/
.App-maze-list {
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 1rem;
  padding: 1rem;
  border: 1px solid var(--primary);
  margin-bottom: 0.5rem;
}

/*------------------- app tmdb -------------------*/
.App-tmdb-list {
  max-width: 50%;
  border: 1px solid var(--primary);
  padding: 1rem;
}

/*------------------- app omdb -------------------*/
.App-omdb-list {
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 1rem;
  padding: 1rem;
  border: 1px solid var(--primary);
}
/*------------------- app footer -------------------*/
.App-footer-icon {
  width: 2.5rem;
  padding: 0.25rem;
}

.App-footer--links a {
  display: inline-block;
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.25rem;
  text-transform: uppercase;
}
.App-footer--links a:hover {
  color: var(--secondary);
}

/*------------------- app tags -------------------*/
.App-tags {
  display: flex;
  gap: 0.5rem;
}
@media (max-width: 768px) {
  .App-tags {
    justify-content: center;
  }
}
.App-tags span {
  background: var(--secondary);
  border-radius: 4rem;
  color: var(--color-neutral-80);
  display: inline-block;
  font-size: 0.75rem;
  letter-spacing: 0.03rem;
  padding: 0.3rem 1.2rem;
  box-shadow: var(--shadow-1);
}

/*------------------- app movie page -------------------*/
.App-moviePage--container {
  display: grid;
  grid-template-columns: auto minmax(auto, 2fr) 1fr;
  grid-template-rows: 295px;
  overflow: hidden;
  /* border: 1px solid; */
}

.App-moviePage--poster {
  height: 295px;
  width: 210px;
  background-position: top center !important;
  background-size: cover !important;
  background-repeat: no-repeat !important;
}
@media (max-width: 1024px) {
  .App-moviePage--container {
    grid-template-columns: auto 1fr;
    grid-template-rows: minmax(300px, auto) auto;
  }
  .App-moviePage--poster {
    height: auto;
  }
  .App-moviePage--buttons {
    grid-column: 1 / span 2;
  }
  .App-watchlist {
    display: flex !important;
    flex-direction: row !important;
    flex-wrap: wrap !important;
    padding: 0.25rem 0;
    margin: 0 !important;
  }
  @media (max-width: 768px) {
    .App-watchlist button > span {
      display: none;
    }
    .App-watchlist {
      justify-content: center;
    }
  }
  .App-watchlist > * {
    margin-right: 0.25rem;
  }
}
/*------------------- app watchlist -------------------*/
.App-watchlist {
  display: flex;
  flex-direction: column;
}

.App-profile--items-container {
  display: flex;
  margin-bottom: 0.4rem;
  flex-direction: column;
}
.App-profile--items,
.App-profile--items-title {
  display: grid;
  grid-template-columns: 1fr 1fr auto;
  margin-bottom: 0.2rem;
}

/*------------------- app profile/watchlist -------------------*/
.App-profile--watchlist-container {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 0.25rem;
  margin-bottom: 0.2rem;
  box-shadow: var(--shadow-1);
  font-size: var(--xsm);
  background-color: #2d2d2d;
}
.App-profile--watchlist-body {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .App-profile--watchlist-container {
  grid-template-columns: 1fr;
  }
  .App-profile--watchlist-body {
    display: flex;
    flex-direction: column;
    align-items: initial;
  }
  .App-profile--watchlist-poster {
    height: 160px !important;
    width: 100px !important;
  }
  .App-profile--watchlist-buttons {
    flex-direction: column;
  }
}
/*------------------- app menu animation -------------------*/
.App-fadeIn li:nth-child(1) {
  animation: fadeIn 0.1s ease-in-out;
}
.App-fadeIn li:nth-child(2) {
  animation: fadeIn 0.2s ease-in-out;
}
.App-fadeIn li:nth-child(3) {
  animation: fadeIn 0.3s ease-in-out;
}
.App-fadeIn li:nth-child(4) {
  animation: fadeIn 0.4s ease-in-out;
}
.App-fadeIn li:nth-child(5) {
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  0% {
    transform: translateY(-1rem);
    opacity: 0;
  }
  100% {
    transform: initial;
    opacity: 1;
  }
}

/*------------------- app footer -------------------*/
footer {
  background-color: #000;
  color: var(--secondary);
  padding: 2rem 0 4rem;
  margin-top: 2rem;
  display: flex;
  justify-content: center;
}
