.App-profile--watchlist {
  position: relative;
  /*  */
  box-shadow: var(--shadow-1);
  display: grid;
  grid-template-columns: 1fr 7fr;
  gap: 1rem;
  margin-bottom: 0.25rem;
}

.App-profile--watchlist-poster {
  /* height: 100px; */
}

.App-profile--watchlist-img {
  height: 75px;
  /* height: 85px; */
  /* width: 65px; */
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center top !important;
}

.App-profile--watchlist-body {
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  padding-right: 0.25rem;
  align-items: center;
}

.App-profile--watchlist-links {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  background: var(--tertiary);
  border-radius: var(--border-radius);
  z-index: 500;
}

.App-profile--watchlist-body-header {
  width: 100%;
}
.App-profile--watchlist-body-buttons {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .App-profile--watchlist {
    grid-template-columns: 1fr auto;
    margin-bottom: 0.5rem;
  }
  .App-profile--watchlist-poster {
    align-self: center;
  }

  .App-profile--watchlist-img {
    height: 267px;
    /* width: 165px; */
  }
}
