/*# sourceMappingURL=bootstrap-grid.min.css.map */
*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  font-size: 100%;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font);
  font-weight: 400;
  line-height: 1.75;
  color: var(--secondary);
  background-color: var(--color-neutral-80);
}
input,
button,
textarea {
  font-family: var(--font);
}

p {
  margin-bottom: 1rem;
  font-size: var(--sm);
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: var(--font);
  line-height: 1.3;
}
/* identical to box height */
h1 {
  font-weight: 700;
  font-size: var(--xxl);
}
h2 {
  font-size: var(--xl);
  background-color: var(--primary);
  background: linear-gradient(to left, #834d9b, var(--primary));

  color: var(--secondary);
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 1.5rem;
}

h3 {
  font-size: var(--lg);
}

h4 {
  font-size: var(--md);
}

h5 {
  font-size: var(--sm);
}

h6 {
  font-size: var(--xsm);
}

small,
.text-small {
  font-size: var(--xsm);
  line-height: 1.5;
  letter-spacing: 0.06rem;
  color: var(--color-neutral-60);
}
label {
  display: block;
  font-size: var(--xsm);
  color: var(--secondary);
  letter-spacing: 0.03rem;
}

b {
  font-weight: 700;
}

.before::before {
  content: '';
  display: inline-block;
  background-color: var(--primary);
  height: 2px;
  width: 2rem;
  margin-right: 1rem;
}

/* - - - - - - links - - - - - - */
a {
  color: var(--primary);
  text-decoration: none;
}

/* - - - - - - img - - - - - - */
img,
svg {
  display: block;
  max-width: 100%;
}
.svg-icon {
  display: block;
  padding: 0.5rem;
  border-radius: var(--border-radius);
}
.poster-small {
  height: 10rem;
  transition: 0.1s ease-in-out;
}

.poster {
  width: 200px;
}

/* - - - - - - input - - - - - - */
input,
button,
textarea {
  padding: 0.75rem;
  outline: none;
  border: none;
  font-size: var(--sm);
  font-family: var(--font);
  letter-spacing: 0.02rem;
}
a,
button,
input,
textarea {
  transition: 0.1s ease-out;
}
button:hover,
a:hover {
  cursor: pointer;
  -webkit-filter: brightness(115%);
          filter: brightness(115%);
}
input,
textarea {
  background-color: var(--color-neutral-70);
  border-bottom: 2px solid var(--secondary);
  color: var(--secondary);
  width: 100%;
  display: block;
}
input:focus,
input:active,
textarea:focus,
textarea:active {
  background-color: var(--color-neutral-80);
  -webkit-filter: brightness(115%);
          filter: brightness(115%);
  border-bottom: 2px solid var(--primary);
}
input:disabled,
textarea:disabled {
  color: var(--color-neutral-60);
  cursor: no-drop;
  border-bottom: 2px solid var(--color-neutral-60);
}
.btn-icon:disabled {
  color: var(--color-neutral-80);
  border-bottom: 2px solid var(--color-neutral-60);
  cursor: no-drop;
}
textarea {
  resize: vertical;
}
.field {
  background-color: var(--color-neutral-70);
  border-radius: var(--border-radius);
}
.field-button {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.field-button--button {
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
}
input[type=radio] {
  width: 1.4rem;
  height: 1.4rem;
}
.form-check {
  display: -webkit-flex;
  display: flex;
  /* align-items: center; */
  vertical-align: middle;
  grid-gap: .5rem;
  gap: .5rem;
}
.form-check label {
  font-size: 1rem;
}
/* - - - - - - table - - - - - - */
table {
  width: 100%;
  border: 2px solid var(--primary);
  margin: 1rem 0;
  overflow-y: auto;
  display: block;
  overflow-x: scroll;
  max-height: 50vh;
}

th {
  font-weight: 600;
  text-align: left;
}

td {
  font-size: var(--xsm);
}

th,
td {
  border-right: 1px solid var(--opacity-15);
  border-bottom: 1px solid var(--opacity-15);
  vertical-align: middle;
  padding: 2px 5px;
}
/* - - - - - - buttons - - - - - - */

button:disabled {
  color: var(--gray);
  background-color: var(--light-gray);
}
.btn {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  font-weight: 400;
  font-size: var(--btn);
  height: 3rem;
  line-height: 3rem;
  letter-spacing: 0.02rem;
  border-radius: var(--border-radius);
  white-space: pre;
}
.btn-icon {
  border-radius: var(--border-radius);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.btn,
.btn-icon,
.btn-inline,
.btn-a {
  border: 2px solid transparent;
}
.btn:active,
.btn-icon:active,
.btn-inline:active,
.btn-a:active {
  border: 2px dashed var(--primary);
}
.btn:focus,
.btn-icon:focus,
.btn-inline:focus,
.btn-a:focus {
  border: 2px dashed var(--primary);
}
.btn-inline {
  -webkit-align-items: center;
          align-items: center;
  border-radius: var(--border-radius);
  display: -webkit-inline-flex;
  display: inline-flex;
  height: 3rem;
  font-size: var(--btn);
  font-weight: 400;
  white-space: pre;
}
/*  */
.btn-inline-small {
  padding: 0.25rem 1rem;
  -webkit-align-items: center;
          align-items: center;
  border-radius: var(--border-radius);
  display: -webkit-inline-flex;
  display: inline-flex;
  font-size: var(--xsm);
  font-weight: 500;
  white-space: pre;
}

.btn-a {
  background-color: transparent;

