@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,400&display=swap');

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  font-size: 100%;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font);
  font-weight: 400;
  line-height: 1.75;
  color: var(--secondary);
  background-color: var(--color-neutral-80);
}
input,
button,
textarea {
  font-family: var(--font);
}

p {
  margin-bottom: 1rem;
  font-size: var(--sm);
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: var(--font);
  line-height: 1.3;
}
/* identical to box height */
h1 {
  font-weight: 700;
  font-size: var(--xxl);
}
h2 {
  font-size: var(--xl);
  background-color: var(--primary);
  background: -webkit-linear-gradient(to left, #834d9b, var(--primary));
  background: linear-gradient(to left, #834d9b, var(--primary));

  color: var(--secondary);
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 1.5rem;
}

h3 {
  font-size: var(--lg);
}

h4 {
  font-size: var(--md);
}

h5 {
  font-size: var(--sm);
}

h6 {
  font-size: var(--xsm);
}

small,
.text-small {
  font-size: var(--xsm);
  line-height: 1.5;
  letter-spacing: 0.06rem;
  color: var(--color-neutral-60);
}
label {
  display: block;
  font-size: var(--xsm);
  color: var(--secondary);
  letter-spacing: 0.03rem;
}

b {
  font-weight: 700;
}

.before::before {
  content: '';
  display: inline-block;
  background-color: var(--primary);
  height: 2px;
  width: 2rem;
  margin-right: 1rem;
}

/* - - - - - - links - - - - - - */
a {
  color: var(--primary);
  text-decoration: none;
}

/* - - - - - - img - - - - - - */
img,
svg {
  display: block;
  max-width: 100%;
}
.svg-icon {
  display: block;
  padding: 0.5rem;
  border-radius: var(--border-radius);
}
.poster-small {
  height: 10rem;
  transition: 0.1s ease-in-out;
}

.poster {
  width: 200px;
}

/* - - - - - - input - - - - - - */
input,
button,
textarea {
  padding: 0.75rem;
  outline: none;
  border: none;
  font-size: var(--sm);
  font-family: var(--font);
  letter-spacing: 0.02rem;
}
a,
button,
input,
textarea {
  transition: 0.1s ease-out;
}
button:hover,
a:hover {
  cursor: pointer;
  filter: brightness(115%);
}
input,
textarea {
  background-color: var(--color-neutral-70);
  border-bottom: 2px solid var(--secondary);
  color: var(--secondary);
  width: 100%;
  display: block;
}
input:focus,
input:active,
textarea:focus,
textarea:active {
  background-color: var(--color-neutral-80);
  filter: brightness(115%);
  border-bottom: 2px solid var(--primary);
}
input:disabled,
textarea:disabled {
  color: var(--color-neutral-60);
  cursor: no-drop;
  border-bottom: 2px solid var(--color-neutral-60);
}
.btn-icon:disabled {
  color: var(--color-neutral-80);
  border-bottom: 2px solid var(--color-neutral-60);
  cursor: no-drop;
}
textarea {
  resize: vertical;
}
.field {
  background-color: var(--color-neutral-70);
  border-radius: var(--border-radius);
}
.field-button {
  display: flex;
  align-items: flex-end;
}
.field-button--button {
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
}
input[type=radio] {
  width: 1.4rem;
  height: 1.4rem;
}
.form-check {
  display: flex;
  /* align-items: center; */
  vertical-align: middle;
  gap: .5rem;
}
.form-check label {
  font-size: 1rem;
}
/* - - - - - - table - - - - - - */
table {
  width: 100%;
  border: 2px solid var(--primary);
  margin: 1rem 0;
  overflow-y: auto;
  display: block;
  overflow-x: scroll;
  max-height: 50vh;
}

th {
  font-weight: 600;
  text-align: left;
}

td {
  font-size: var(--xsm);
}

th,
td {
  border-right: 1px solid var(--opacity-15);
  border-bottom: 1px solid var(--opacity-15);
  vertical-align: middle;
  padding: 2px 5px;
}
/* - - - - - - buttons - - - - - - */

button:disabled {
  color: var(--gray);
  background-color: var(--light-gray);
}
.btn {
  display: flex;
  align-items: center;
  width: 100%;
  font-weight: 400;
  font-size: var(--btn);
  height: 3rem;
  line-height: 3rem;
  letter-spacing: 0.02rem;
  border-radius: var(--border-radius);
  white-space: pre;
}
.btn-icon {
  border-radius: var(--border-radius);
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn,
.btn-icon,
.btn-inline,
.btn-a {
  border: 2px solid transparent;
}
.btn:active,
.btn-icon:active,
.btn-inline:active,
.btn-a:active {
  border: 2px dashed var(--primary);
}
.btn:focus,
.btn-icon:focus,
.btn-inline:focus,
.btn-a:focus {
  border: 2px dashed var(--primary);
}
.btn-inline {
  align-items: center;
  border-radius: var(--border-radius);
  display: inline-flex;
  height: 3rem;
  font-size: var(--btn);
  font-weight: 400;
  white-space: pre;
}
/*  */
.btn-inline-small {
  padding: 0.25rem 1rem;
  align-items: center;
  border-radius: var(--border-radius);
  display: inline-flex;
  font-size: var(--xsm);
  font-weight: 500;
  white-space: pre;
}

.btn-a {
  background-color: transparent;

  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: var(--border-radius);
}

.btn-primary {
  background-color: var(--primary);
  color: var(--secondary);
}

.btn-secondary {
  background-color: var(--secondary);
  color: var(--color-neutral-80);
}

.btn-warning {
  background-color: var(--warning);
  color: var(--color-neutral-80);
}

.btn-info {
  background-color: var(--info);
  color: var(--secondary);
}

.btn-success {
  background-color: var(--success);
  color: var(--white);
}

.btn-error {
  background-color: var(--error);
  color: var(--white);
}
.btn-pink {
  background-color: var(--pink);
  color: var(--white);
}

.btn-transparent {
  background-color: transparent;
  color: var(--secondary);
}

.btn-a-primary {
  color: var(--primary-var);
  background-color: transparent;
}
.btn-a-secondary {
  color: var(--secondary);
  background-color: transparent;
}

.btn-outline-error {
  color: var(--error);
  background-color: transparent;
}

.link-search {
  background: var(--secondary);
  color: var(--color-neutral-80);
  display: block;
  border-bottom: 2px solid var(--primary);
  margin-bottom: 2px;
  padding: 0.5rem;
}

/* - - - - - - small - - - - - - */
@media (max-width: 768px) {
  .sm-none {
    display: none;
  }
}
/* - - - - - - helper - - - - - - - - - */

.block {
  display: block;
  width: 100%;
}
.flex {
  display: flex;
}
.flex-0 {
  flex: 0;
}
.flex-1 {
  flex: 1;
}
.flex-align-end {
  align-items: flex-end;
}
.flex-align-center {
  align-items: center;
}
.flex-justify-start {
  justify-content: flex-start;
}
.flex-justify-center {
  justify-content: center;
}
.flex-justify-end {
  justify-content: flex-end;
}
.flex-justify-between {
  justify-content: space-between;
}
.flex-column {
  flex-direction: column;
}
/* response */
@media (max-width: 576px) {
  .flex-justify-center--sm {
    justify-content: center;
  }
}
.gap-0 {
  gap: 0.25rem;
}
.gap-1 {
  gap: 0.5rem;
}
.gap-2 {
  gap: 1.5rem;
}
.wrap {
  flex-wrap: wrap;
}
.w-100 {
  width: 100%;
}

.relative {
  position: relative;
}
.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
/* - - - - - - - - -  alert - - - - - - - - -  */
.alert {
  display: flex;
  padding: 12px;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.43;
  border-radius: var(--border);
  letter-spacing: 0.01071rem;
  width: 100%;
  align-items: center;
  gap: 0.5rem;
}

.alert-error {
  color: rgb(250, 179, 174);
  background-color: rgb(24, 6, 5);
}

.alert-warning {
  color: rgb(255, 213, 153);
  background-color: rgb(25, 15, 0);
}
.alert-success {
  color: rgb(183, 223, 185);
  background-color: rgb(7, 17, 7);
}
.alert-info {
  color: rgb(166, 213, 250);
  background-color: var(--opacity-15);
}

.svg-hover:hover svg path {
  padding: 0.65rem 0.75rem;
  fill: var(--primary);
}
/* - - - - - - border - - - - - - */
.b-1 {
  border: 1px dashed;
}
.b-2 {
  border: 2px dashed;
}
